.section {
  &--black {
    background-color: var(--dark);
  }

  &--light {
    background-color: var(--light-main);
    background-color: #f5f5f5;
  }

  &:not(:last-child) {
    margin-bottom: 80px;
  }

  &__content {
    border-radius: 35px;
    background-color: #000000;
    padding: var(--content-offset);
  }
}